<template>
  <!--代发货-配送-确定收货-->
  <div class="ship left">
    <home-header :title="'订单状态'" :back="true"></home-header>
    <div class="ship-main">
      <div class="ship-main-product">
        <div class="ship-main-item">订单状态：<span>{{info.status}}<!--{{statusList | filterStatus(info.status)}}--></span></div>
        <div class="ship-main-item">订单号：<span>{{info.orderNo}}</span></div>
        <div class="ship-main-item">订单总金额：<span>￥{{info.payment}}.00元</span></div>
        <div class="ship-main-item ship-item-time">下单时间：<span>{{info.createTime | filterTime}}</span></div>
        <div class="ship-main-pro" v-for="(item,index) in info.productList" :key="index">
          <div class="ship-main-image">
            <img :src="item.img" alt="">
          </div>
          <div class="ship-main-content">
            <div class="ship-main-title">
              {{item.name}}
            </div>
            <div class="ship-main-subtitle">
              {{item.size}}
            </div>
            <div class="ship-main-money">
              <p>小计：<span>￥{{item.money}}元</span>数量：{{item.count}}</p>
              <div class="ship-pro-btn" @click="apply" v-if="info.status>=5">
                申请售后
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="ship-main-message" v-if="info.status == 4">
        <div class="ship-message-title">
          我的评论
        </div>
        <div class="ship-message-evaluate">
          <div class="ship-evaluate-title">
            商品描述：
          </div>
          <div class="ship-evaluate-val">
            <div class="ship-evaluate-li" v-for="(item,index) in 5" :key="index" @click="activeStar(index)"></div>
          </div>
          <div class="ship-evaluate-val" v-if="starCount">
            <div class="ship-active-li" v-for="(item,index) in starCount" :key="index" @click="activeStar(index)"></div>
          </div>
        </div>
        <div class="ship-message-input">
          <textarea v-model="message" maxlength="50" placeholder="请输入评论内容"></textarea>
        </div>
      </div>
      <div class="ship-main-button">
        <span @click="submitBtn">{{statusBtn | filterBtn(info.status)}}</span>
      </div>
      <div class="ship-main-address">
        <div class="ship-address-item">
          收货人姓名：<span>{{info.shippingDto.receiverName}}</span>
        </div>
        <div class="ship-address-item">
          手机号：<span>{{info.shippingDto.receiverMobile}}</span>
        </div>
        <div class="ship-address-item txt">
          <span class="detailTxt">详细地址：</span>
          <p class="detailAddress">{{info.shippingDto.receiverProvince}}省{{info.shippingDto.receiverCity}}{{info.shippingDto.receiverDistrict}}{{info.shippingDto.receiverAddress}}</p>
        </div>
        <div class="ship-address-total">
          <p>商品总价：<span>￥{{info.payment}}.00元</span> - 折扣：<span>￥{{}}元</span></p>
          <p>快递费：<span>￥{{}}元</span></p>
          <p>应付款金额：<span>￥{{}}元</span></p>
        </div>
      </div>
      <div class="ship-main-address">
        <div class="ship-address-item">
          所选支付方式：<span>易宝支付</span>
        </div>
        <div class="ship-address-item">
          应付款金额：<span class="tagcolor">￥{{}}元</span>
        </div>
      </div>
      <div class="ship-main-address">
        <div class="ship-address-item">
          配送方式：<span>快递</span>
        </div>
        <div class="ship-address-item">
          快递单号：<span>{{}}</span>
        </div>
      </div>


    </div>
  </div>
</template>

<script>
  import homeHeader from '../../components/header/header.vue'
  import {filterTime} from "../../mixins/filter";
  import {MessageBox} from 'mint-ui';

  export default {
    data() {
      return {
        starCount: 0,
        message: '',
        bindCard:null,
        payPopupShow: false,
        addActive: false,
        bankList:[],
        bankNum:'',
        bankType:'',
        orderNo:'',
        bindId:'',
        bankCode:'',
        id:'',
        info:null,
        statusList: ['已确定', '未付款', '已付款', '未发货', '已发货', '收货确定'],
        statusBtn: ['', '支付', '', '', '确定收货', '已完成'],
      }
    },
    //注释过滤器
    filters: {
      filterStatus: function (val, list) {
        let status = '';
        for (var i = 0, l = list + 1; i < l; i++) {
          if (i < l - 1) {
            if (i % 2 == 0) {
              status += val[i] + ' ';
            }
          } else {
            status += val[i]
          }
        }
        return status;
      },
      filterBtn: function (val, list) {
        return val[list];
      },
      filterTime: filterTime
    },
    computed: {},
    components: {
      homeHeader,
    },
    updated() {
      console.log(this.starCount);
    },
    methods: {
      activeStar(val) {
        this.starCount = val + 1;
      },
      getStatus() {
        let that = this;
        var memberId=window.localStorage.getItem('memberId')
        that.id = that.$route.query.orderNumber;
        that.ajax({
          url:'/order/showOrder',
          method:'post',
          data:{
            memberId:memberId,
            orderId:that.id
          },
          success:function(res){
            if(res.status==1000){
              that.info=res.data[0];
              if(that.info.status==30){
                that.info.status='待发货'
              }
              if(that.info.status==60){
                that.info.status='待收货'
              }
            }
          }
        })
      },
      apply() {
        MessageBox({
          title: '请致电客服',
          message: '<a href="tel:18768496225">18768496225</a>',
        });
      },
      submitBtn() {
        let that = this;
        switch (that.info.status) {
          case 1:

            break;
          case 4:

            break;
          case 5:
            MessageBox({
              title: '订单已完成',
              message: '',
            });
            break;
          default:
            return;
        }
      }
    },
    mounted() {

    },
    created() {
      this.getStatus()
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../style/minxin";

  .ship-main {
    padding: $top 0;
  }

  .ship-item-time {
    padding-bottom: 15px;
  }
  .ship-main-product {
    padding: 15px 10px;
    border-bottom: 1px solid $border;
    @include sh($med, 1.6em);
    color: $gray;
    background: $f;
    .ship-main-pro {
      display: flex;
      flex-direction: row;
      padding: 5px 5px 10px;
      border-bottom: 1px solid $border;
      &:last-child {
        border-bottom: 0;
      }
      .ship-main-image {
        @include wh(5rem, 5rem);
        img {
          @include wh(5rem, 5rem);
        }
      }

      .ship-main-content {
        padding-left: 10px;
        flex: 1;
        .ship-main-title {
          @include sh($med, 1.5em);
          height: 3em;
          @include el(2);
        }
        .ship-main-subtitle {
          @include sh($min, 1em);
        }
        .ship-main-money {
          @include sh($min, 2em);
          p {
            float: left;
          }
          span {
            color: $red;
            padding-right: 5px;
          }
          .ship-pro-btn {
            float: right;
            border: 1px solid $red;
            padding: 0 10px;
            @include sh($min, 2em);
            border-radius: 3px;
            color: $red;
          }
        }
      }
    }
  }

  /*我的评论*/
  .ship-main-message {
    background: $f;
    padding: 10px;
    border-bottom: 1px solid $border;
    .ship-message-title {
      padding-bottom: 15px;
    }
    .ship-message-evaluate {
      @include sh($med, 1em);
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      padding-bottom: 15px;
      position: relative;
      .ship-evaluate-title {
        padding-top: 2px;
      }
      .ship-evaluate-val {
        position: absolute;
        left: 5rem;
        top: 0;
        .ship-evaluate-li {
          float: left;
          margin-right: 5px;
          @include wh($max, $max);
          @include bg('../../assets/img/2.png');
        }
        .ship-active-li {
          float: left;
          margin-right: 5px;
          @include wh($max, $max);
          @include bg('../../assets/img/0.png');
        }
      }
    }
    .ship-message-input {
      border: 1px solid $border;
      textarea {
        padding: 5px;
        @include sh($min, 1.6em);
        @include wh(100%, 5em);
      }
    }
  }

  .ship-main-button {
    padding: 10px;
    span {
      display: block;
      @include sh($max, 2.4em);
      background: $red;
      color: $f;
      text-align: center;
      border-radius: 3px;
      letter-spacing: 1px;
    }
  }

  .ship-main-address {
    border-top: 1px solid $border;
    border-bottom: 1px solid $border;
    background: $f;
    padding: 0 10px;
    margin: 6px 0;
    .txt{
      overflow: hidden;
      padding:10px 0;
      border-bottom: 1px solid $border;
      .detailTxt{
        float:left;
        font: 0.94rem/1.2rem "Microsoft YaHei";
      }
      .detailAddress{
        width:80%;
        font: 0.94rem/1.2rem "Microsoft YaHei";
        float:right;
      }
    }
    .ship-address-item {
      @include sh($med, 3em);
      border-bottom: 1px solid $border;
      color: $gray;
      span {
        font-weight: normal;
        color: #000;
      }
      &:last-child {
        border: 0;
      }
    }
    .ship-address-total {
      padding: 10px 0;
      p {
        text-align: right;
        @include sh($min, 2em);
        span {
          color: $red;
        }
      }
    }
  }


</style>



